$primary-font: Barlow, "sans-serif";
$section1-heading-color: #f48120;
$section2-heading-color: #626296;
$section2-paragraph-color: #b8b8e5;


%container-style {
  display: flex;
  text-align: left;
}

%h1-style {
  font: $primary-font;
  font-size: 6vw;
  font-weight: 900;
  line-height: 0.9em;
  padding: 0;
  margin: 0;
  margin-bottom: 4%;
}

%p-style {
  font-size: 1.6vw;
  font-weight: 500;
  font: $primary-font;
}

#section2 {
  height: fit-content;
  padding: 5%;
  background-color: #8080bb;

  #sec2-container {
    @extend %container-style;

    #sec2-column-left {
      flex-basis: 55%;
    }
    h1 {
      @extend %h1-style;
      font-size: 4vw;
      color: $section2-heading-color;
    }

    p {
      @extend %p-style;
      color: $section2-paragraph-color;
      padding-top: 5%;
    }

    span {
      color: white;
    }

    img {
      margin-top: 5%;
      width: 60%;
    }
  }
  

  #sec2-column-right{
    flex-basis:45%;
    display: flex;
    flex-direction: column;
    
    

    .lottie-animation {
     width: 95%;
     height: auto;
     padding: 2%;
    }

  }
}

