$primary-font: Barlow, "sans-serif";
$section1-heading-color: #f48120;

%form-style {
  font-size: 1.2vw;
  border: none;
  align-items: center;
  justify-content: center;
  display: flex;
  padding: 0;
  height: 3vw;
  font-weight: 900;
  font: $primary-font;
}

%container-style {
  display: flex;
  text-align: left;
}

%h1-style {
  font: $primary-font;
  font-size: 6vw;
  font-weight: 900;
  line-height: 0.9em;
  padding: 0;
  margin: 0;
  margin-bottom: 5%;
}

%p-style {
  font-size: 1.6vw;
  font-weight: 500;
  font: $primary-font;
}

#section1 {
  padding: 5%;
  padding-bottom: 20%;
  padding-top: 6%;
  background-color: #ffad34;
  height: fit-content;

  #section1-container {
    @extend %container-style;
    .sec1-column {
      flex-basis: 50%;
    }
    #sec1-column-left {
      z-index: 2;
    }
    h1 {
      @extend %h1-style;
      color: $section1-heading-color;
    }

    .form-container {
      display: flex;
      width: 70%;
    }

    input {
      @extend %form-style;
      color: white;
      border-radius: 99px 0 0 99px;
      width: 60%;
      border-color: white;

      &::placeholder {
        color: #ffce87;
        padding-left: 2%;
      }
    }

    button {
      @extend %form-style;
      width: 40%;
      color: white;
      background-color: $section1-heading-color;
      border-color: $section1-heading-color;
      border-radius: 0px 99px 99px 0px;
    }
  }

  #sec1-column-right {
    position: relative;
    flex-basis: 50%;
    z-index: 1;
  }
  img {
    width: 115%;
    height: auto;
    position: absolute;
    top: -35%;
    left: -20%;
    z-index: 1;
  }
}
